@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Readex+Pro:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Kanit:wght@100&family=Readex+Pro:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.logo-imagen{
    margin-top: -1rem;
    margin-bottom: -1rem;
    width: 8rem;
    height: 8rem;
}

.tituloCategoria{
    color: whitesmoke;
    padding: 1rem;
    font-size: 1.25rem;
}

.tituloCategoria:hover{
    color:rgb(250, 116, 62)
}
.itemListContainer{
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    grid-template-rows: 1fr;
    margin-top: 1rem;
    margin-left: 1rem;
    
}

.item-individual{
    margin-left: 1rem;
    margin-right: 1rem;
    margin-bottom: 1rem;
}

.imagenProducto{
    margin-top: -1rem;
    margin-bottom: -1.75rem;
    padding: 1rem;
}

.card-body{
    margin: 0rem;
    padding: 0rem;
}

.card-title{
    margin: 0rem;
    color: #000000;
    font-family: 'Kanit', sans-serif;
    font-weight: bold;
}

a{
    text-decoration: none;
    color: whitesmoke;
}

a:hover{
    color:rgb(250, 116, 62)
}

.itemListLoading{
    text-align: center;
    grid-column-start: 1;
    grid-column-end: 5;
    font-family: 'Kanit', sans-serif;
    font-weight: bold;
    font-size: 3rem;
}

.card-text{
    margin: 0rem;
    margin-bottom: 1rem;
    color: #000000;
    font-family: 'Kanit', sans-serif;
    font-weight: lighter;
    font-weight: bold;
}

button{
    margin-bottom: 0.5rem;
    font-family: 'Kanit', sans-serif;
}

.itemImg{
    height: auto;
    width: 40%;;
    margin-bottom: 0rem;
    margin-top: 0rem;
}

.buttonDetalle{
    margin-left: 1rem;
    margin-right: 1rem;
    border-radius: 0.25rem;
}

.buttonFinal{
    margin-left: 0.25rem;
    margin-right: 0.25rem;
    border-radius: 0.25rem;
}

.listaCart{
    display: grid;
    grid-template-columns: 10% 20% 20% 20% 20% 10%;
    grid-template-rows: 100%;
    border: 1px solid black;
    margin: 1rem;
    align-items: center;
    font-family: 'Kanit', sans-serif;
    font-weight: bold;
}

.cartImagen{
    width: 50%;
}

.totalCompra{
    font-family: 'Kanit', sans-serif;
    font-weight: bold;
    margin-top: -1rem;
    margin-bottom: 1rem;
    font-size: 2rem;
}

.deleteItem{
    font-size: 1rem;
    border: none;
}

.deleteItem:hover{
    background-color: rgb(250, 116, 62);
    border: none;
}

.cantidadWidget{
    font-size: 1rem;
    margin-top: 2rem;
    background-color: rgb(250, 116, 62);
    width: 1.5rem;
    font-family: 'Kanit', sans-serif;
    font-weight: bold;
    color: black
}

.carritoVacio{
    font-family: 'Kanit', sans-serif;
    font-weight: bold;
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.formularioCompra{
    padding-top: 1rem;
    border: 1px solid black;
    font-family: 'Kanit', sans-serif;
    background-color: #262626;
    font-weight: bold;
    font-size: 1.5rem;
    color: whitesmoke
}

.inputFormulario{
    margin-top: 1rem;
    width: 70%;
    border-radius: 0.25rem;
    font-family: 'Kanit', sans-serif;
    font-size: 1rem;
}

.buttonConfirmarOrden{
    margin-top: 1rem;
}

footer{
    height: 1.5rem;
    background-color: #262626;
    font-family: 'Kanit', sans-serif;
    color: whitesmoke;
    text-align: right;
}

.containerFooter{
    margin-right: 1rem;
    display: flex;
    justify-content: right;
    align-items: center;
    font-size: 1rem;
}

.far{
    margin-left: 0.5rem;
}






